._main-container {
    padding: 2rem;
    background: #fff;
}

.main-container {
    margin: 2rem 0 !important;
}

.image-container h1 {
    display: none;
    text-align: center;
    margin-bottom: 1rem;
}

.tabs-container{
    margin-top: 1.5rem;
}

@media (max-width: 768px) {
    .info-container {
        margin-top: 1rem;
    }

    .info-container h1 {
        display: none;
    }

    .image-container h1 {
        display: block;
    }
  }